.slide-div {
    color: black;
    text-align: center;
    width: 100%;
}

.item-image {
    width: 25vw;
    height: 20vw;
}

.item-div:hover {
    animation: changeColor 0.5s forwards;
}

@keyframes changeColor {
    from {color: black;}
    to {color: red;}
}

.slick-prev:before, .slick-next:before {
    font-size: 30px !important;
}

.slick-prev {
    left: -35px !important;
}

.headerItems {
    color: floralwhite;
    font-weight: bold;
}